import Vue from "vue";
import VueRouter from "vue-router";

import chartarea from "@/components/chartarea";
import chartdealamount from "@/components/chartdealamount";
import chartdealtype from "@/components/chartdealtype";
import chartgu from "@/components/chartgu";
import App from "@/App";


Vue.use(VueRouter);
export const router = new VueRouter({
   mode:'history',
    routes: [
        {
            path:'/',
            name: 'index',
            component: App
        },
        {
            path:'/area',
            name: 'area',
            component: chartarea
        },
        {
            path:'/dealamount',
            name: 'dealamount',
            component: chartdealamount
        },
        {
            path:'/dealtype',
            name: 'dealtype',
            component: chartdealtype
        },
        {
            path:'/gu',
            name: 'gu',
            component: chartgu
        }
    ]
});