<template>
    <div>
        <div id="areaForChart" class="container">
            <g-chart style="width: 100%; height: 500px"
                     type="PieChart"
                     :data="inputdata"
                     :options="chartOptions"
            ></g-chart>
        </div>
    </div>
</template>

<script>

    import axios from "@/util/axios";
    import {GChart} from 'vue-google-charts';

    export default {
        name: "chartdealtype",
        components: {
            GChart
        },
        data() {
            return {
                inputdata: [],
                chartOptions: {
                    title: '거래 방식별 거래건',
                    is3D: true,
                    titleTextStyle: {
                        fontSize: 25
                    },
                    colors: ['#355c7d', '#6c5b7b', '#c06c84', '#f67280', '#f8b195', '#6A8AA6', '#F2C9C9'],
                    legend: {position: 'bottom', textStyle: {color: 'black', fontSize: 19}}
                }
            }
        },
        props: {
            date: String,
            type: String
        },
        mounted() {
            axios
                .get('?date=' + this.date + '&type=' + this.type)
                .then((response) => {
                    let chartdata = response.data;
                    let title = ['DealType', 'The number of section'];
                    this.inputdata.push(title);

                    for (var area of chartdata) {
                        let newdata = [area.name, area.count];
                        this.inputdata.push(newdata);
                    }

                    console.dir(this.inputdata);
                })
                .catch((error) => {
                    console.dir(error);
                });
        }
    }
</script>

