                            <b-form-select v-model="type">
                                <b-form-select-option value="1">지역별(구)</b-form-select-option>
                                <b-form-select-option value="2">거래방식별</b-form-select-option>
                                <b-form-select-option value="3">크기별</b-form-select-option>
                                <b-form-select-option value="4">가격대별</b-form-select-option>
                            </b-form-select>
                        </b-col>
