                        </b-col>
                    </b-form-row>
                </div>
            </div>
            <div id="chartSpace" class="container">
                <router-view :date="startDate.replace('-', '')+endDate.replace('-', '')"
                             :type="type"></router-view>
                <footer></footer>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "@/util/axios";

    export default {
        name: 'App',
        data() {
            return {
                startDate: "",
                endDate: "",
                type: 1,
                userid: ''
            }
        },
        components: {},
        methods: {
            getChart() {
                let start = Math.floor(this.startDate.replace('-', '') / 100) * 12 + this.startDate.replace('-', '') % 100;
                let end = Math.floor(this.endDate.replace('-', '') / 100) * 12 + this.endDate.replace('-', '') % 100;


                if (start > end) {
                    alert("검색 기간을 다시 확인해주세요.");
                    return;
                }

                if (this.type == 1)
                    this.$router.push('/gu');
                else if (this.type == 2)
                    this.$router.push('/dealtype');
                else if (this.type == 3)
                    this.$router.push('/area');
                else if (this.type == 4)
                    this.$router.push('/dealamount');
                else
                    alert("타입값 오류");
            },
            logout() {
                axios
                    .get('/logout')
                    .then(() => {
                        this.userid = '';
                    })
                    .catch((error) => {
                        console.dir(error);
                    })
            }
        },
        mounted() {
            axios
                .get('/logined')
                .then((response) => {
                    let name = response.data;
                    if (name != null) {
                        this.userid = name;
                    }
                })
                .catch((error) => {
                    console.dir(error);
                });
            // 로그인이 되어있는지는 userid가 ''면 비로그인, userid가 ''이 아니면 로그인 상태로 판별
        }
    }
</script>

